import {
  render,
  screen,
  fireEvent,
  act,
  waitFor,
} from '@testing-library/react-native';
import 'react-native';
import React from 'react';
import FindPokemon from '../UI/components/FindPokemon';

describe('Test search pokemon component', () => {
  test('Component renders correctly with error prop false', async () => {
    const error = false;
    const whichMock = jest.fn();
    const tree = render(<FindPokemon which={whichMock} isError={error} />);
    await waitFor(() =>
      expect(screen.getByText('Search For A Pokemon')).toBeInstanceOf(),
    );
  });
});
